import { Separator } from "@/components/ui/separator"
import { SidebarInset, SidebarTrigger } from "@/components/ui/sidebar"
import { ThemeProvider } from "@/components/theme-provider"
import { ThemeToggle } from "@/components/theme-toggle"
import { DynamicBreadcrumb } from "@/components/navigation/dynamic-breadcrumb"
import { AppSidebar, AppSidebarProvider } from "@/features/navigation"
import { ActiveLineProvider } from "@/features/navigation/components/active-line-context"
import { mapLinesToNavigationOptions } from "@/features/navigation/config/navigation-config"
import { getDistinctLineIds } from "@/features/line-dashboard/api/get-line-ids"
import "./globals.css"

export const metadata = {
  title: "Create Next App",
  description: "Generated by create next app",
}

export default async function RootLayout({ children }) {
  let lineOptions = []

  try {
    lineOptions = await getDistinctLineIds()
  } catch {
    lineOptions = []
  }

  const lines = mapLinesToNavigationOptions(lineOptions)

  return (
    <html lang="en" suppressHydrationWarning>
      <body>
        <ThemeProvider>
          <AppSidebarProvider>
            <ActiveLineProvider lineOptions={lines}>
              <AppSidebar lines={lines} />
              <SidebarInset>
                <header
                  className="flex h-16 shrink-0 items-center justify-between gap-2 px-4 transition-[width,height] ease-linear group-has-data-[collapsible=icon]/sidebar-wrapper:h-12"
                >
                  <div className="flex items-center gap-2">
                    <SidebarTrigger className="-ml-1" />
                    <Separator orientation="vertical" className="mr-2 data-[orientation=vertical]:h-4" />
                    <DynamicBreadcrumb />
                  </div>
                  <ThemeToggle />
                </header>
                <main className="flex min-w-0 flex-1 flex-col gap-4 p-4 pt-0">
                  {children}
                </main>
              </SidebarInset>
            </ActiveLineProvider>
          </AppSidebarProvider>
        </ThemeProvider>
      </body>
    </html>
  )
}
